<style lang="less" scoped>
  .videoWrap {
    width: 100%;
    height: 350rpx;
    background-color: rgba(0, 0, 0, 0.5);
  }

  // 录像文件样式
  .replayFileContainer {
    .replayFileTitle {
      padding: 0 20rpx;
      box-sizing: border-box;
      font-size: 15px;
      height: 80rpx;
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .replayIcon {
        .more {
          display: flex;
          align-items: center;
          .fileIcon {}
          .moreInfo {
            width: 45rpx;
          }
        }
      }
      .title {
        display: flex;
      }
    }

    .replayListWrap {
      flex-direction: column;
      width: 100%;
      flex: 1;
      flex-shrink: 0;
      overflow-y: scroll;
      .replayItem {
        width: 100%;
        height: 140rpx;
        background-color: #fff;
        margin-top: 10rpx;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 20rpx;
        box-sizing: border-box;

        .leftWrap {
          flex: 1;
          display: flex;
          align-items: center;
          font-size: 14px;

          .imageCheckbox {
            transform: scale(0.8);
          }
        }

        .rightWrap {
          display: flex;
          align-items: center;
          position: relative;
          width: 180rpx;
          height: 120rpx;

          .imageIcon {
            width: 180rpx;
            height: 120rpx;
            border-radius: 10rpx;
          }

          .isLock {
            width: 54rpx;
            height: 54rpx;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -27rpx;
            margin-left: -27rpx;
            z-index: 999;
          }
        }
      }
    }

    // 时间样式
    .timerWrap {
      width: 100%;
      height: 80rpx;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 20rpx;
      box-sizing: border-box;
      background-color: #fff;
      border-top: 1px solid #eee;

      .settingTime {
        display: flex;
        align-items: center;

        .timerIcon {
          margin-right: 15rpx;
          width: 36rpx;
          height: 36rpx;
        }

        .timerText {
          font-size: 14px;
        }
      }

      .fileIcon {
        display: flex;
        align-items: center;

        .fileIconTitle {
          font-size: 14px;
          padding: 8rpx 25rpx;
          box-sizing: border-box;
          border: 1px solid #ccc;
        }

        .icon1 {
          border-right: none;
          border-top-left-radius: 10rpx;
          border-bottom-left-radius: 10rpx;
        }

        .imageIcon {
          border-left: none;
          border-top-right-radius: 10rpx;
          border-bottom-right-radius: 10rpx;
        }
      }
    }

    .operationWrap {
      height: 80rpx;
      background-color: #fff;
      margin-top: 10rpx;
      display: flex;
      align-items: center;
      font-size: 14px;

      .listItem {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        .allselectLabel {
          display: flex;
          align-items: center;

          .allSelect {
            transform: scale(0.8);
          }
        }

        .lockSwith {
          transform: scale(0.8);
        }

        .loakTitle {
          margin-left: -8rpx;
        }

      }

      .loadupWrap {
        image {
          width: 48rpx;
          margin-right: 10rpx;
        }
      }

      .deleteWrap {
        image {
          width: 45rpx;
          margin-right: 10rpx;
        }
      }
    }

    .imageCheckbox /deep/ .uni-checkbox-wrapper {
      .uni-checkbox-input {
        border-radius: 50% !important
      }
    }
  }
</style>
<template>
  <view class="livePlayWrap">
    <view class="videoWrap">
      <!-- 视频实时播放插件 -->
      <view class="livePlayContainer"></view>
      <!-- 视频回放插件 -->
      <view class="backPlayContainer"></view>
    </view>
    <view class="replayFileContainer">
      <!-- 录像文件控制栏 -->
      <view class="replayFileTitle">
        <view class="title">
          <view>录像文件</view>
        </view>
        <view class="replayIcon">
          <view class="more">
            <image class="moreInfo" src="../../static/index/more.png" mode="widthFix" @click="showOpertBarEvent">
            </image>
          </view>
        </view>
      </view>
      <view class="timerWrap">
        <view class="settingTime">
          <image class="timerIcon" src="../../static/index/calendar-dark.png"></image>
          <picker mode="date" :value="date" :start="startDate" :end="endDate" @change="getDateChange">
            <view class="timerText">{{date === currentTimer ? "今天" : date}}</view>
          </picker>
        </view>
        <view class="fileIcon">
          <view class="fileIconTitle icon1">前路</view>
          <view class="fileIconTitle afterTitle">后路</view>
          <view class="fileIconTitle imageIcon">图片</view>
        </view>
      </view>
      <!-- 显示操作列表 -->
      <view class="operationWrap" v-if="isShowOpertBar">
        <view class="listItem allSelectWrap">
          <label class="allselectLabel" @click="allOpertEvent">
            <checkbox class="allSelect" />
            <text>全选</text>
          </label>
        </view>
        <view class="listItem lockWrap">
          <switch class="lockSwith" checked="false" color="#DC851F" />
          <text class="loakTitle">锁定</text>
        </view>
        <view class="listItem loadupWrap">
          <image src="../../static/index/upload.png" mode="widthFix"></image>
          <view class="title">下载</view>
        </view>
        <view class="listItem deleteWrap">
          <image src="../../static/index/delete.png" mode="widthFix"></image>
          <view class="title">删除</view>
        </view>
      </view>
      <!-- 列表数据 -->
     <scroll-view class="replayListWrap" scroll-y @scrolltolower="getScrollBackPlayData" :style="{height:scrollH}">
        <view class="replayItem" v-for="(backPlayItem, index) in backPlayList" :key="index">
          <view class="leftWrap">
            <checkbox class="imageCheckbox" v-if="isShowOpertBar" />
            <view class="timer">{{backPlayItem.StartTime}}</view>
          </view>
          <view class="rightWrap">
            <image class="imageIcon" src="../../static/image/xsdz.png"></image>
            <image class="isLock" src="../../static/index/lock.png"></image>
          </view>
        </view>
      </scroll-view>
    </view>

  </view>
</template>

<script>
  export default {
    data() {
      return {
        isShowOpertBar: false, // 默认不显示操作栏
        replayLists: [{

        }],
        date: "",
        currentTimer: "",
        findFileSavSize: 0,
        maxItemPerPage: 0,
        backPlayList: [], // 回放列表数组
        wHeight: 0,
        scrollH: ""
        
      }
    },
    computed: {
      startDate() {
        return this.getDate('start')
      },
      endDate() {
        return this.getDate('end')
      }
    },
    onLoad(val) {
      // 获取可使用窗口的高度
      uni.getSystemInfo({
        success: (res) => {
          console.log(301, res)
          this.wHeight = res.windowHeight
        }
      })
      // 设置导航栏标题
      uni.setNavigationBarTitle({
        title: val.deviceName
      })
      // 获取当前时间
      this.currentTimer = this.$common.timer()
      // 获取回放列表
      this.getMediaFileDestory()
    },
    mounted() {
      this.date = this.getDate({
        format: true
      })
      // 设置scroll-view的高度
      this.scrollH = (this.wHeight - 80 - 175) + "px"
      console.log(322, this.scrollH)
    },
    methods: {
      // 向上滑动获取数据
      getScrollBackPlayData () {
        console.log(310310)
        // 触底请求数据, 即为上拉加载更多数据
        let len = this.backPlayList.length
        let pages = Math.ceil(this.findFileSavSize / 13)
        if (len <= pages * 13) {
          this.getMediaFileNextFile()
        } 
        if (len ===  this.findFileSavSize) {
          uni.showToast({
            title:"数据加载完成",
            mask: true
          })
        }
      },
      // 获取回放列表
      getMediaFileDestory() {
        let params = {
          "method": "mediaFileFind.destory",
          "params": {
            "token": 0,
          },
          "session": 81375,
          "id": 1
        }
        this.$axios(params).then(res => {
          if (res.data.result) {
            this.getMediaFileFindCreate()
          }
        })
      },
      getMediaFileFindCreate() {
        let params = {
          "method": "mediaFileFind.factory.create",
          "params": null,
          "id": 8597,
          "session": "19dd79c0115a980e4db95b0f4be8a902"
        }
        this.$axios(params).then((res) => {
          if (res.data.result) {
            this.getMediaFileFind()
          }
        })
      },
      // 获取总数据found, 最大页数 maxItemPerPage
      getMediaFileFind() {
        const currentDate = this.getdateInfo()
        let params = {
          "method": "mediaFileFind.findFile",
          "params": {
            "token": 0,
            "condition": {
              "Dirs": ["/mnt/sdcard"],
              "Types": ["sav"],
              "Flags": ["Timing", "Event", "Alarm", "Manual"],
              "StartTime": `${currentDate} 00:00:00`,
              "EndTime": `${currentDate} 23:59:59`
            }
          },
          "id": 8597,
          "session": 123456
        }
        this.$axios(params).then((res) => {
          if (res.data.result) {
            this.findFileSavSize = res.data.params.found
            this.maxItemPerPage = res.data.params.maxItemPerPage
            this.getMediaFileNextFile()
          }
          
        })
      },
      getMediaFileNextFile () {
        uni.showLoading({
          title: "数据加载中",
          mask: true
        })
        let params = {
          "method":"mediaFileFind.findNextFile",
          "params":{
            "count":13,
            "token":0,
          },
          "id":8600,
          "session":"19dd79c0115a980e4db95b0f4be8a902"
        }
        this.$axios(params).then(res => {
          if (res.data.result) {
            this.backPlayList = this.backPlayList.concat(res.data.params.infos)
            uni.hideLoading()
          }
        })
      },
      // 获取当前时间
      getDateChange(e) {
        this.date = e.target.value
        console.log(123, this.date)
      },
      getdateInfo () {
        const date = new Date()
        let year = date.getFullYear()
        let month = date.getMonth() + 1
        let day = date.getDate()
        return `${year}-${month}-${day}`
      },
      // 获取时间
      getDate(type) {
        const date = new Date()
        let year = date.getFullYear()
        let month = date.getMonth() + 1
        let day = date.getDate()
        if (type === "start") {
          year = year - 60
        } else if (type === "end") {
          year = year + 2
        }
        month = month > 9 ? month : "0" + month
        day = day > 9 ? day : "0" + day
        return `${year}-${month}-${day}`
      },
      // 跳转到记录仪设置页面
      toSkipSetting() {
        this.$uniapi.goGavigate('../setting/setting')
      },
      // 点击更多显示操作栏
      showOpertBarEvent() {
        this.isShowOpertBar = !this.isShowOpertBar
      },
      // 全选事件
      allOpertEvent() {

      }
    }
  }
</script>
